.navMenu{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  border: 0.3rem solid blue;
  border-radius: 15px;
  height: 35vh;
  min-height: 15vw;
  max-width: 20vw;
  min-width: 20vw;
  margin: 1%;
}

.navButtons{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.navTextBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  overflow-wrap: break-word;
}

.navText{
  font-size: 1vw;
}
#navTextLink{
  font-weight: 700;
}

.topDiv{
  display: flex;
  flex-direction: row;
}

.mapDiv{
}

.navSubMenu{
  padding-top: 10px;
}

.mapDiv{
  width: 100%;
}

.map-tooltip {
  font-weight: 400;
  background-color: white;
  border-radius: 4px;
  box-shadow: -3px 2px 26px -7px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -3px 2px 26px -7px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -3px 2px 26px -7px rgba(0, 0, 0, 0.75);
  padding: 6px 12px;
}

.map-tooltip-name{
  font-weight: 500;
}

.map-tooltip-error{
  font-style: italic;
}

.TabContainers{
  display: none;
  width: 40vw;
}